<template>
  <div class="hello">
  <TransitionGroup tag="ul" name="fade" class="container list-group">
      <li class="list-group-item" v-for="(list, index) in lists" :key="index">
      {{ list.text }}<a href="#" class="float-end" v-on:click="deleteList(index)"><i class="bi bi-x"></i></a>
      </li>
    </TransitionGroup>
    <form>
    <div class="row mt-4">
      <div class="col-10">
        <div class="form-floating">
          <textarea class="form-control" placeholder="Leave a comment here" id="floatingTextarea" v-model="listText"></textarea>
          <label for="floatingTextarea">Comments</label>
        </div>
      </div>
      <div class="col-2">
        <button type="button" class="btn btn-primary col-12 h-100" v-on:click="addList()">Primary</button>
      </div>
    </div>
    <div class="row mt-4">
      <div class="col-2">
        <button type="button" class="btn btn-warning col-12 h-100" v-on:click="resetList()">Reset</button>
      </div>
    </div>
    </form>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data: function () {
    var lists;
    let listsz = JSON.parse(localStorage.getItem('lists'));
    if(listsz == null){
      lists = [
          {text: 'Default List 1'},
          {text: 'Default List 2'}
        ];
      localStorage.setItem('lists',JSON.stringify(lists));
    }
    return {
      listText: '',
      lists: listsz
    }
  },
   methods: {
		addList: function() {
			var newList = this.listText.trim();
			if (!newList) {return;}
			this.lists.push(
				{text: newList}
			);
      localStorage.setItem('lists',JSON.stringify(this.lists));
			this.listText = '';
		},
    resetList: function(){
      localStorage.removeItem('lists');
      this.lists = [
          {text: 'Default List 1'},
          {text: 'Default List 2'}
        ];
      localStorage.setItem('lists',JSON.stringify(this.lists));
    },
    deleteList: function(a){
      this.lists.splice(a, 1);
      localStorage.setItem('lists',JSON.stringify(this.lists));
    }
   }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
/* 1. declare transition */
.fade-move,
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
}

/* 2. declare enter from and leave to state */
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
  transform: scaleY(0.01) translate(30px, 0);
}

/* 3. ensure leaving items are taken out of layout flow so that moving
      animations can be calculated correctly. */
.fade-leave-active {
  position: absolute;
}
</style>
